@import url(https://fonts.googleapis.com/css?family=Roboto:500);

.App {
    text-align: center;
    padding: 2% 5%;
    max-width: 1200px;
    margin: 0 auto;
}

.App * {
    box-sizing: border-box;
}

ul {
    padding: 1%;
    list-style: none;
}

p {
    margin: 5px 0;
}

input[type="text"],
input[type="number"] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    align-self: center;
    height: 38px;
    margin-bottom: 20px;
}

.city {
    width: 250px;
}

#city-search {
    display: flex;
    flex-direction: column;
    margin: auto;
    position: relative;
}

.suggestions {
    width: 250px;
    position: absolute;
    top: 45px;
    z-index: 2;
    align-self: center;
    margin: 0;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
}

.suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
    background-color: #bcdcdb;
}

#event-list {
    padding: 0;
    max-width: 800px;
    margin: 0 auto;
  }

.event {
    padding: 20px;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 8px;
    position: relative;
    background: #fff;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    transition: transform 0.2s ease;
}

.location {
    color: #666;
    font-style: italic;
}

.event-created {
    color: #888;
    font-size: 0.9em;
}

.details-btn {
    position: absolute;
    right: 5px;
    bottom: 5px;
    padding: 5px 8px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease;
}

.details-btn:hover {
    background: #e0e0e0;
}

.alerts-container {
    position: fixed;
    top: 0px;
    left: 20px;
    width: 250px;
    z-index: 1000;
}

