@media (max-width: 768px) {
    .App {
        padding: 1rem;
      }

      .event {
        margin: 10px;
        padding: 15px;
      }
    
      #event-list {
        grid-template-columns: 1fr;
      }
      
      .city {
        width: 100%;
        max-width: 250px;
      }

      .event-summary h2 {
        font-size: 1.2em;
      }

}

    
@media (max-width: 480px) {
    
      
    h1 {
        font-size: 24px;
    }
}